import { styled } from "@rp/react/web/mui/core"
import { KeyboardArrowDown } from "@rp/react/web/mui/icons"

type TriangleArrowType = {
  isOpen: boolean
}
export const TriangleArrow = styled(KeyboardArrowDown, {
  name: "TriangleArrow",
  shouldForwardProp: props => props !== "isOpen",
})<TriangleArrowType>(({ theme, isOpen }) => ({
  width: "1em",
  height: "1em",
  display: "inline-block",
  fill: "currentcolor",
  flexShrink: 0,
  transition: "fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
  fontSize: "1.5rem",
  position: "relative",
  right: "0px",
  top: "calc(50% - 0.5em)",
  color: theme.palette.common.white,
  transform: isOpen ? "rotate(180deg)" : "",
}))
